body {
  background-color: #f4f4f4;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

.header {
  color: #fff;
  text-align: center;
  background-color: #4caf50;
  padding: 20px;
}

.header h1 {
  margin: 0;
  font-size: 36px;
}

nav {
  margin-top: 10px;
}

nav ul {
  justify-content: center;
  padding: 0;
  list-style: none;
  display: flex;
}

nav ul li {
  margin: 0 15px;
}

nav ul li a {
  color: #fff;
  font-size: 18px;
  font-weight: bold;
  text-decoration: none;
}

nav ul li a:hover {
  color: #ff0;
}

.body-container {
  text-align: center;
  margin-top: 20px;
}

.crypto-cards {
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  display: flex;
}

.card {
  text-align: center;
  word-wrap: normal;
  -ms-border-radius: 10px;
  -o-border-radius: 10px;
  background-color: #f9f9f9;
  border: 2px solid pink;
  border-radius: 10px;
  width: 250px;
  padding: 15px;
  transition: transform .3s ease-in-out;
  box-shadow: 5px 5px 10px #0000001a;
}

.card:hover {
  transform: scale(1.05);
}

.card img {
  border-radius: 50%;
  width: 100px;
}

.card h2 {
  font-size: 20px;
}

.card p {
  font-size: 18px;
  font-weight: bold;
}

.card a {
  color: #00f;
  font-weight: bold;
  text-decoration: none;
}

.footer {
  text-align: center;
  color: #fff;
  background-color: #222;
  margin-top: 20px;
  padding: 10px;
}
